@font-face {
    font-family: "Relay Cond Regular";
    src: url("../fonts/RelayCond-Regular.woff") format("woff"),
         url("../fonts/RelayCond-Regular.woff2") format("woff2");
    font-display: swap;
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes argh-my-eyes {
    0% {
        color: #8a6d3b;
        background-color: #fff;
        border-color: #faebcc;
    }

    49% {
        color: #8a6d3b;
        background-color: #fff;
        border-color: #faebcc;
    }

    50% {
        color: #a94442;
        background-color: #f2dede;
        border-color: #ebccd1;
    }

    99% {
        color: #a94442;
        background-color: #f2dede;
        border-color: #ebccd1;
    }

    100% {
        color: #8a6d3b;
        background-color: #fff;
        border-color: #faebcc;
    }
}

*, :after, :before {
    box-sizing: border-box;
}

.secondaryHeadline {
    clear: both;
}

.row,body {
    box-direction: normal;
}

h1,h2 {
    font-weight: 500;
    line-height: 1.1;
    color: inherit;
    margin-top: 20px;
    margin-bottom: 10px;
}

h1 {
    font-size: 36px;
    margin: .67em 0;
}

h2 {
    font-size: 30px;

    a {
        color: #E50000;
    }
}

html,body,h1,h2,p,a,span {
    font-family: "Relay Cond Regular", Helvetica, Arial, sans-serif;
}

p {
    font-size: 18px;
    margin: 0 0 10px;
}

html {
    font-size: 10px;
    text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
    margin: 0;
    padding-top: 15.1em;
    font-weight: 300;
    font-size: 10px;
    line-height: 1.42857143;
    color: #333;
    background-color: #fff;
    transition: all .5s ease;

    .header-stay-collapsed:not(.home) {

        @media (max-width:30em) {
          padding-top: 4.3em;
        }
    }

    @media (max-width:46.875em) {
        padding-top: 14.1em;
    }

    @media (max-width:30em) {
        padding-top: 11.5em;
    }
}

article,aside,figure,header,nav,section {
    display: block;
}

a {
    background-color: transparent;
    text-decoration: none;
    color: #337ab7;

    &:active, &:hover {
        outline: 0;
    }

    &:hover, &:focus {
        color: #23527c;
        text-decoration: underline;
    }

    &:focus {
        outline: thin dotted;
        outline: 5px auto -webkit-focus-ring-color;
        outline-offset: -2px;
    }
}

.header-trending-story {

    a:active, a:hover, a:active, a:hover {
      text-decoration: underline;
    }
}

b, strong {
    font-weight: bold;
}

svg {
      &:not(:root) {
        overflow: hidden;
    }
}

figure {
    margin: 0;
}

hr {
    box-sizing: content-box;
    height: 0;
    margin-top: 20px;
    margin-bottom: 20px;
    border: 0;
    border-top: 1px solid #eee;
}

input,select {
    color: inherit;
    font: inherit;
    margin: 0;
}

input {
    line-height: normal;

    &::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
}

label {
    display: inline-block;
    max-width: 100%;
    margin-bottom: 5px;
    font-weight: bold;
}

select {
    text-transform: none;
}

.clearfix:before,.clearfix:after,.row:before,.row:after,.form-horizontal .form-group:before,.form-horizontal .form-group:after {
    display: table;
    content: " ";
}

.clearfix:after,.row:after,.form-horizontal .form-group:after {
    clear: both;
}

.header-nav-primary,.header-trending h2 {
    text-transform: uppercase;
}

.header-nav-primary,.header-nav-secondary-utility li,.header-trending h2,.header-trending-story,.header-trending-tags {
    font-family: museo-sans,Helvetica,Arial,sans-serif;
}

.header-nav-secondary-utility li,.header-trending h2,.header-trending-story,.header-trending-tags {
    font-weight: 500;
}

.header-nav-primary {
    font-weight: 700;
}

.article-head .article-info .article-byline {
    font-family: museo-sans,Helvetica,Arial,sans-serif;
    font-weight: 900;
}

.ad-sponsor {
    display: inline-flex;
}

.container-medium {
    flex: 0 1 110em;

    @media (max-width:68.75em) {
        flex-basis: 100%;
        padding: 0 1em;
    }
}

.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

img {
    display: block;
    width: 100%;
    height: auto;
    border: 0;
}

abbr[title] {
    cursor: help;
    border-bottom: 1px dotted #777;
}

.aligncenter {
    margin: auto;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    z-index: 3;
    width: 100%;
    background: #1a2b36;
    box-direction: normal;
    box-orient: vertical;

    ul {
        list-style: none;
        margin-top: 0;
        margin-bottom: 10px;
    }

    .color-theme-primary-fill {
        fill: #1a2b36;
    }

    .color-theme-secondary {
        color: #ec008c;
    }

    .color-theme-secondary-fill-hover {
        &:hover {
            fill: #ec008c;
        }
    }

    .color-theme-secondary-down-arrow {
        &:after {
            border-top-color: #ec008c;
        }
    }

    .color-theme-dek {
        color: #fff200;
    }

    .color-theme-hed {
        color: #fff;
    }
}

.header .row:first-child .container-medium {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 110em;
    padding: 1.8em 0 0 14.8em;
    transition: all .5s ease;
}

@media (max-width:30em) {
    .header-stay-collapsed:not(.home) .header .row:first-child .container-medium {
        padding: 0 0 0 14.8em;
    }
}

@media (max-width:70em) {
    .header .row:first-child .container-medium {
        padding-left: 15.8em;
        padding-right: 1em;
    }
}

@media (max-width:65.625em) {
    .header .row:first-child .container-medium {
        padding-left: 13.8em;
        padding-top: 1.6em;
    }
}

.header-trending {
    position: relative;
    bottom: .7em;
    flex-grow: 1;
    flex-shrink: 1;
    width: 0;
    max-height: 10em;
    padding-right: 2em;
    transition: all .5s ease;
    opacity: 1;

    h2 {
      margin: 0 0 .5em;
      font-size: 1.2em;
      line-height: 1;
    }

    @media (max-width:30em) {
        bottom: auto;
    }
}

@media (max-width:30em) {
    .header .row:first-child .container-medium {
        padding-left: 9.1em;
        padding-top: .6em;
        padding-bottom: .6em;
    }

    .header-stay-collapsed:not(.home) .header-trending {
        overflow: hidden;
        max-height: 0;
        opacity: 0;
        padding-top: 0;
        padding-bottom: 0;
    }
}

.header-trending-story,.header-trending-tags {
    font-size: 1.5em;
    -webkit-font-smoothing: antialiased;
}

.header-trending-tags {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    height: 1em;
    padding: 0;
    margin: 0 0 .8em;
    line-height: 1;

    li {
        margin-right: 1em;
    }

    @media (max-width:30em) {
        margin-bottom: 0;
    }
}

.header-trending-story {
    margin: 0;
    line-height: 1.2;

    &:before {
        display: inline-block;
        content: attr(data-title);
        opacity: 0;
    }

    a {
        position: absolute;
        top: 2.73333em;
        left: 0;
    }

    @media (max-width:30em) {
        display: none;
    }
}

.header-nav {
    width: 100%;
    background: #000;
    background: rgba(0,0,0,.45);

    .container-medium {
        display: flex;
        position: relative;
        width: 110em;
        transition: all .5s ease;

        @media (max-width:68.75em) {
            width: 100%;
        }
    }
}

.header-nav-secondary {
    width: 32.6em;
    max-height: 10em;
    padding-bottom: 1.8em;
    transition: all .5s ease;
    opacity: 1;

    @media (max-width:46.875em) {
        width: auto;
    }
}

@media (max-width:30em) {
    .header-stay-collapsed:not(.home) .header-nav .container-medium {
        padding-left: 6.1em;
        padding-right: 1em;
    }

    .header-nav-secondary {
        padding: .8em 0;
    }
}

.header-nav-secondary-utility {
    display: flex;
    justify-content: flex-end;
    padding: 0;
    margin: 0 0 1.6em;

    li {
        padding-left: .83333em;
        border-left: .11111em solid rgba(0,0,0,.65);
        margin-left: .83333em;
        font-size: 1.8em;
        line-height: 1;

        &:first-child {
            -webkit-box-ordinal-group: 3;
            order: 2;

            a {
                padding-right: 1.33333em;
                color: rgba(0,0,0,.65);

                &:hover {
                    color: #fff;
                }

                span {
                    @media (max-width:46.875em) {
                        display: none;
                    }
                }

                svg {
                    path {
                        @media (max-width:46.875em) {
                            fill: rgba(0,0,0,.65);
                        }
                    }
                    @media (max-width:46.875em) {
                        position: relative;
                        top: auto;
                        width: 2.27778em;
                        height: 2.27778em;
                    }
                }

                @media (max-width:46.875em) {
                    padding-right: 0;
                }
            }

            @media (max-width:46.875em) {
                padding-left: 0;
                border-left: 0;
                margin-left: 0;
            }
        }

        &:last-child {
            -webkit-box-ordinal-group: 2;
            order: 1;
            padding-left: 0;
            border-left: none;
            margin-left: 0;

            @media (max-width:46.875em) {
                display: none;
            }
        }
    }

    a {
        position: relative;

        svg {
            position: absolute;
            top: -.33333em;
            width: 1.75em;
            height: 1.75em;
            transition: all .5s ease;
        }

        &:link, &:visited {
            color: rgba(0,0,0,.65);

            &:path {
                fill: rgba(0,0,0,.65);
            }
        }

        &:active, &:hover {
            text-decoration: none;
            color: #fff;
            transition: all .5s ease;

            &:path {
                fill: #fff;
                transition: all .5s ease;
            }
        }
    }

    @media (max-width:46.875em) {
        padding: 1em 0;
        margin-bottom: 0;
    }

    @media (max-width:30em) {
        padding: 0;
    }
}

.header-nav-secondary-social {
    display: flex;
    justify-content: space-between;
    padding: 0 5.8em 0 0;
    margin: 0;

    &.no-search {
        padding: 0 0 0 5.8em;
    }

    @media (max-width:46.875em) {
        display: none;
    }
}

.header-nav-secondary-social.no-search li:last-of-type a svg .icon-background {
    fill: #fff;
}

.header-nav-secondary-social.no-search li:last-of-type a svg .color-theme-primary-fill {
    color: #1a2b36;
}

.header-nav-secondary-social.no-search li:last-of-type a.social-icons:after path,.header-nav-secondary-social.no-search li:last-of-type a.social-icons:hover path {
    fill: #ec008c;
}

.header-nav-secondary-social li a svg {
    width: 3.7em;
    height: 3.7em;
}

.header-nav-secondary-social li a svg>* {
    fill: #1a2b36;
    fill: rgba(0,0,0,.65);
    transition: all .5s ease;
}

.header-nav-secondary-social li a:active .icon-facebook .icon-background,.header-nav-secondary-social li a:hover .icon-facebook .icon-background {
    fill: #3b5999;
}

.header-nav-secondary-social li a:active .icon-twitter .icon-background,.header-nav-secondary-social li a:hover .icon-twitter .icon-background {
    fill: #00acee;
}

.header-nav-secondary-social li a:active .icon-pinterest .icon-background,.header-nav-secondary-social li a:hover .icon-pinterest .icon-background {
    fill: #cb2028;
}

.header-nav-secondary-social li a:active .icon-google-plus .icon-background,.header-nav-secondary-social li a:hover .icon-google-plus .icon-background {
    fill: #dd4c39;
}

.header-nav-secondary-social li a.social-icons:after path,.header-nav-secondary-social li a.social-icons:hover path {
    fill: #fff;
}

@media (max-width:30em) {
    .header-stay-collapsed:not(.home) .header-nav-secondary {
        overflow: hidden;
        max-height: 0;
        padding: 0;
        opacity: 0;
    }
}

.header-nav-logo {
    position: absolute;
    left: 0;
    top: -9.5em;
    display: block;
    width: 10em;
    height: 5.2em;
    background: url(../img/sprites1.png) no-repeat;
    background-position: -114px 0;
    transition: all .5s ease;

    span {
        width: 0;
        height: 0;
        display: block;
        text-indent: -999em;
    }

    @media (max-width:70em) {
        left: 1em;
    }

    @media (max-width:46.875em) {
        top: -9.2em;
    }

    @media (max-width:30em) {
        top: -9.5em;
        width: 4.1em;
        height: 3.3em;
        transform: translateY(10.2em);
        background-position: -224px -9px;
    }
}

.header-nav-primary>li.header-nav-primary-more>div:after {
    width: 0;
    height: 0;
    border-left: .3em solid transparent;
    border-right: .3em solid transparent;
    border-top: .46667em solid;
}

.header-nav-primary {
    display: flex;
    min-width: 33.73333em;
    padding: 0;
    margin: 0;
    font-size: 1.5em;
    line-height: .8;
    -webkit-font-smoothing: antialiased;

    &>li {
        &:nth-child(2) {
            @media (max-width:25.125em) {
                display: none;
            }
        }

        &:nth-child(5) {
            @media (max-width:31.875em) {
                display: none;
            }
        }

        &:nth-child(6) {
            @media (max-width:41.875em) {
                display: none;
            }
        }
    }

    @media (max-width:32.8125em) {
        min-width: auto;
    }
}

.header-nav-primary>li>a:link,.header-nav-primary>li>a:visited {
    display: block;
    outline: 0;
}

.header-nav-primary>li>a,.header-nav-primary>li>div {
    padding: 1em .66667em .66667em;
    border-bottom: .4em solid transparent;
    letter-spacing: .06667em;
    transition: all .25s ease-out;
}

.header-nav-primary>li>a:active,.header-nav-primary>li>a:hover,.header-nav-primary>li>div:hover {
    border-bottom: .4em solid #ec008c;
    background: #ec008c;
}

.header-nav-primary>li>div {
    cursor: pointer;
}

.header-nav-primary>li.header-nav-primary-more {
    position: relative;
}

@media (max-width:30em) {
    .header-stay-collapsed:not(.home) .header-nav-primary {
        min-width: auto;
    }

    .header-nav-primary>li.header-nav-primary-more {
        position: static;
    }
}

.header-nav-primary>li.header-nav-primary-more>div {
    min-width: 5.53333em;
}

.header-nav-primary>li.header-nav-primary-more>div:hover {
    background: 0 0;
    border-bottom: .4em solid #000;
}

.header-nav-primary>li.header-nav-primary-more>div:after {
    position: relative;
    top: .13333em;
    float: right;
    display: block;
    margin-left: .4em;
    content: ' ';
}

@media (max-width:30em) and (max-width:28.25em) {
    .header-stay-collapsed:not(.home) .header-nav-primary>li:nth-child(2) {
        display: none;
    }
}

@media (max-width:30em) and (max-width:23.25em) {
    .header-stay-collapsed:not(.home) .header-nav-primary>li:nth-child(4) {
        display: none;
    }
}

@media (max-width:30em) and (max-width:41.875em) {
    .header-stay-collapsed:not(.home) .header-nav-primary>li:nth-child(6) {
        display: none;
    }
}

.header-nav-trending {
    flex-grow: 1;
    width: 0;
    padding-right: 2em;
    opacity: 0;
    transition: all .5s ease;

    @media (max-width:50em) {
        display: none;
    }
}

.header-nav-trending-tags {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    height: 2.86667em;
    padding: 0;
    margin: 0;
    font-size: 1.5em;
    line-height: 1;
    pointer-events: none;

    a {
        &:link, &:visited {
            display: block;
            padding: .93333em .66667em;
            outline: 0;
            color: rgba(255,255,255,.6);
            transition: color .25s ease;
        }

        &:active, &:hover {
            color: #fff;
        }
    }

    @media (max-width:41.875em) {
        display: none;
    }
}

@media (max-width:30em) {
    .header-stay-collapsed:not(.home) .header-nav-trending-tags {
        pointer-events: auto;
    }

    .header-stay-collapsed:not(.home) .header-nav-trending {
        width: 0;
        opacity: 1;
    }
}

.article-main {
    width: 95%;
    padding: 0 10px 2em;
    position: relative;
    background: #fff;

    a {
        &:hover {
            text-decoration: underline;
        }
    }

    @media (min-width:68.75em) {
        padding: 0 2em 5.5em;
    }

    @media (min-width:62em) {
        width: 100%;
    }
}

.article-head {
    header {
        padding: 2.7em 0 .5em;

        h1 {
            margin: 0;
            font-size: 5em;
            line-height: 1.06;

            @media (max-width:50em) {
                width: auto;
                font-size: 3.8em;
            }

            @media (max-width:30em) {
                letter-spacing: -1.5px;
                line-height: 1;
            }
        }
    }

    .article-info {
        font-size: 1.3em;

        .article-byline {
            font-weight: 700;
            color: #ec008c;
        }
    }
}

.sue-panel em {
    font-size: 15px;
}

.su-box-title {
    font-size: 15px;
    padding: 5px 5px 5px 5px;
}

.su-box-content {
    padding: 5px;
    font-size: 14px;
}

/* FLASHING BANNER START */
#flash-banner {
    z-index: 9999;
    position: fixed;
    bottom: 0;
    right: 0;
    background: #FFF;
    padding: 12px 24px;
    margin-right: 200px;
    color: #3c763d;
    background-color: #fff;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border: 1px solid #fff;
    text-align: center;
    animation: fadein 5s;

    @media screen and (max-width: 767px) {
        width: 100%;
        margin-right: 0;
    }
}

.flashing {
    animation: argh-my-eyes 1s infinite;
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1;
}

.small {
    font-size: 85%;
}

.text-center {
    text-align: center;
}

.col-lg-3,.col-lg-8 {
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

@media (min-width: 1200px) {
    .col-lg-3,  .col-lg-8 {
        float: left;
    }

    .col-lg-3 {
        width: 25%;
    }
}

.form-control {
    display: block;
    width: 100%;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;

    &:focus {
        border-color: #66afe9;
        outline: 0;
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, .6);
    }
    
    &::-moz-placeholder {
        color: #999;
        opacity: 1;
    }

    &:-ms-input-placeholder {
        color: #999;
    }

    &::-webkit-input-placeholder {
        color: #999;
    }

    &::placeholder {
        color: #999;
    }
}

.form-group {
    margin-bottom: 15px;
}

.form-horizontal .form-group {
    margin-right: -15px;
    margin-left: -15px;
}

@media (min-width: 768px) {
    .form-horizontal .control-label {
        padding-top: 7px;
        margin-bottom: 0;
        text-align: right;
    }
}

.comments {
    padding: 10px 0;
    font-size: 14px;
    color: #1d2129;
    background: #fff;
    margin-bottom: 20px;
    font-family: Helvetica, Arial, sans-serif;
    line-height: 1.358;
    word-break: break-word;
    word-wrap: break-word;

    ul {
        padding: 0;
    }

    h3 {
        font-size: 14px;
        font-weight: 900;
        margin-bottom: 8px;
        margin-top: 10px;
        line-height: 20px;
        border-top: 1px solid #e9eaed;
        padding-top: 10px;
    }

    a {
        font-weight: bold;
        color: #365899;
    }

    .comment {
        position: relative;
        display: block;
        margin-bottom: 10px;
        padding-bottom: 10px;
        border-bottom: 1px solid #e9eaed;

        .profile-image {
            position: absolute;
            top: 5px;
            left: 0;
            width: 40px;
            height: 38px;
            background: url(../img/sprites1.png) no-repeat;
